import {
  DollarSign,
  Edit,
  MoreVertical,
  Search,
  Clipboard,
} from "react-feather";
import { FormattedMessage } from "react-intl";
import { Link } from "react-router-dom";
import {
  Badge,
  DropdownItem,
  DropdownMenu,
  DropdownToggle,
  UncontrolledDropdown,
} from "reactstrap";

const statusObj = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Active" />,
  },
  // 0: {
  //   class: "light-warning",
  //   text: <FormattedMessage id="Deactive" />,
  // },
  2: {
    class: "light-secondary",

    text: <FormattedMessage id="Blocked" />,
  },
};

const statusOpen = {
  1: {
    class: "light-success",
    text: <FormattedMessage id="Opened" />,
  },
  0: {
    class: "light-warning",
    text: <FormattedMessage id="Not opened" />,
  },
  3: {},
};

export const columns = [
  {
    name: <FormattedMessage id="Image" />,
    minWidth: "120px",
    selector: "image",
    cell: (row) => (
      <div class="avatar me-1" width="32" height="32">
        <img src={row?.image} alt="" width="32" height="32" />
      </div>
    ),
  },
  {
    name: <FormattedMessage id="Cloud name" />,
    minWidth: "120px",
    selector: "name",
    cell: (row) => row?.product_names[0]?.name,
  },
  {
    name: <FormattedMessage id="VAT" />,
    minWidth: "120px",
    selector: "vat",
    cell: (row) => (row?.vat ? row.vat + " %" : 0 + " %"),
  },
  // {
  //   name: <FormattedMessage id="cloud_type" />,
  //   minWidth: "170px",
  //   selector: "product_type",
  //   cell: (row) => {
  //     if (row?.product_type === 3) {
  //       return "Cloud";
  //     } else {
  //       return "Others";
  //     }
  //   },
  // },
  {
    name: <FormattedMessage id="Link Developement" />,
    minWidth: "120px",
    selector: "url_ytb",
    cell: (row) => {
      let maxLength = 30;
      let contentString = row.product_descriptions[0].sort_description;
      if (contentString.length > maxLength) {
        contentString = contentString.slice(0, maxLength) + "...";
      }
      return row?.url_ytb;
    },
  },
  {
    name: <FormattedMessage id="short_content" />,
    minWidth: "120px",
    selector: "sort_description",
    cell: (row) => {
      let maxLength = 45; // Giả sử độ dài tối đa của chuỗi là 20 ký tự.
      let contentString = row.product_descriptions[0].sort_description;
      if (contentString.length > maxLength) {
        contentString = contentString.slice(0, maxLength) + "...";
      }
      return (
        <span
          dangerouslySetInnerHTML={{
            __html: contentString,
          }}
        ></span>
      );
    },
  },
  {
    name: <FormattedMessage id="status" />,
    minWidth: "170px",
    selector: "status",
    cell: (row) => (
      <Badge className="text-capitalize" color={statusObj[row.status]?.class}>
        {statusObj[row.status]?.text}
      </Badge>
    ),
  },
  // {
  //   name: <FormattedMessage id="Allow" />,
  //   minWidth: "170px",
  //   selector: "allow_display",
  //   cell: (row) => (
  //     <Badge className="text-capitalize" color={statusOpen[row.open]?.class}>
  //       {statusOpen[row.open]?.text}
  //     </Badge>
  //   ),
  // },
  {
    name: <FormattedMessage id="actions" />,
    minWidth: "100px",

    cell: (row) => {
      return (
        row?.id && (
          <UncontrolledDropdown>
            <DropdownToggle tag="div" className="btn btn-sm">
              <MoreVertical size={14} className="cursor-pointer" />
            </DropdownToggle>
            <DropdownMenu right>
              <DropdownItem
                tag={Link}
                to={`/apps/categoryCloud/edit/${row.id}`}
                className="w-100"
              >
                <Edit size={14} className="mr-50" />
                <span className="align-middle">
                  <FormattedMessage id="edit" />
                </span>
              </DropdownItem>

              <DropdownItem
                className="w-100"
                tag={Link}
                to={`/apps/categoryCloud/price/list/${row.id}`}
              >
                <DollarSign size={14} className="mr-50" />
                <span className="align-middle">
                  <FormattedMessage id="price" />
                </span>
              </DropdownItem>

              <DropdownItem
                className="w-100"
                tag={Link}
                to={`/apps/categoryCloud/demo/list/${row.id}`}
              >
                <Clipboard size={14} className="mr-50" />
                <span className="align-middle">
                  <FormattedMessage id="Demo & sample code" />
                </span>
              </DropdownItem>
            </DropdownMenu>
          </UncontrolledDropdown>
        )
      );
    },
  },
];
